er {\n        margin-left: 5% !important;\n        width: 90% !important;\n      }\n    }\n\n    &.supporter {\n      background-position: 18% -196px;\n      .membership-content {\n        background-color: rgba(0, 208, 217, 0.5);\n      }\n    }\n\n    &.memberships {\n      background-position: 58% -176px;\n      .membership-content {\n        background-color: rgba(230, 25, 112, 0.5);\n      }\n    }\n  }\n\n  @media (max-width: $breakpoint-small) {\n    background-size: cover !important;\n    background-position: unset !important;\n    .membership-content {\n      padding: var(--spacing-s);\n    }\n  }\n}\n\n.portal-tagline {\n  margin-bottom: 12px;\n  font-size: 17px;\n  margin-left: 4px;\n}\n\n.membership-wrapper-title {\n  display: flex;\n  margin-top: -3px;\n  margin-bottom: var(--spacing-l);\n  @media (max-width: $breakpoint-small) {\n    margin-top: var(--spacing-s);\n    margin-bottom: 18px;\n  }\n\n  svg {\n    width: 3rem;\n    height: 3rem;\n    background-color: var(--color-header-background);\n    border-radius: 50%;\n    padding: var(--spacing-s);\n    margin-right: 0;\n  }\n  label {\n    font-size: var(--font-large);\n    font-weight: var(--font-weight-bold);\n    padding-left: 10px;\n    // width:auto;\n    margin-top: var(--spacing-s);\n    flex: 1;\n    border-bottom: 1px solid var(--color-header-background);\n  }\n\n  button {\n    height: 36px;\n    margin-top: -6px;\n    float: right;\n    padding-left: var(--spacing-xs);\n    padding-right: var(--spacing-s);\n    .button__content {\n      .button__label {\n        font-size: var(--font-body);\n        margin-left: 0;\n      }\n    }\n    .icon {\n      background-color: unset;\n      width: 2rem;\n      height: 2rem;\n      padding: 8px;\n      padding-left: 0;\n      padding-right: 0;\n      margin: 0;\n    }\n  }\n}\n"],"sourceRoot":""}]);
